import { Button } from "@mui/material";
import { Component, createRef, RefObject } from "react";
import { Norm } from "./modals";
import { NormView } from "./norm-view";

function range(size: number, startAt: number = 0): ReadonlyArray<number> {
	return Array.from(Array(size).keys()).map((i) => i + startAt);
}
interface RowVirtualizerProps {
	rows: Norm[];
	normId: string;
	onTopIndexChange: (index: number) => any;
}

interface RowVirtualizerState {
	normIndex: number;
	isRendering: boolean;
	renderStartIndex: number;
	renderEndIndex: number;
}

export class RowVirtualizer extends Component<
	RowVirtualizerProps,
	RowVirtualizerState
> {
	ticking = false;
	divElement: RefObject<HTMLDivElement>;
	intersectionObserver: IntersectionObserver;
	observedRows = new Set<HTMLDivElement>();
	visibleRows = new Set<HTMLDivElement>();
	topIndex = -1;

	constructor(props: RowVirtualizerProps) {
		super(props);
		this.divElement = createRef();
		this.state = {
			...this.getStateByNormId(),
			isRendering: false,
		};
		this.intersectionObserver = new IntersectionObserver(
			(e, o) => this.handleIntersectionObserver(e, o),
			{ rootMargin: "-64px 0px 0px 0px" }
		);
	}

	getStateByNormId() {
		const normIndex = this.props.rows?.findIndex(
			({ index }) => index === this.props.normId
		);
		const renderStartIndex = Math.max(0, (normIndex || 0) - 10);
		const renderEndIndex = Math.min(
			renderStartIndex + 20,
			this.props.rows?.length || 0
		);
		return { normIndex, renderStartIndex, renderEndIndex };
	}

	componentDidUpdate(prevProps: RowVirtualizerProps) {
		if (
			prevProps.normId !== this.props.normId ||
			prevProps.rows !== this.props.rows
		) {
			const patchState = this.getStateByNormId();
			this.setState(patchState);
			this.updateNormIndex(patchState.normIndex);
		}
		if (prevProps.rows !== this.props.rows) {
			// Reset observer when rows reload
			this.intersectionObserver.disconnect();
			this.observedRows = new Set();
		}
	}

	updateNormIndex(normIndex: number) {
		if (this.props.rows?.length && normIndex !== -1)
			setTimeout(() => {
				const elem = document.querySelector<HTMLElement>(
					`[id=row-${normIndex}]`
				);
				if (elem) {
					this.setState({ isRendering: true });
					elem.scrollIntoView({ block: "start" });
					window.scrollBy({ top: -64 });
					this.setState({ isRendering: false });
				}
				// this.checkTopIndex();
			});
	}

	handleIntersectionObserver(
		entries: IntersectionObserverEntry[],
		observer: IntersectionObserver
	) {
		entries.forEach((entry) => {
			// Update visibleRows
			if (entry.isIntersecting) {
				this.visibleRows.add(entry.target as HTMLDivElement);
			} else {
				this.visibleRows.delete(entry.target as HTMLDivElement);
			}
		});

		console.log(entries);
		setTimeout(() => this.checkTopIndex(), 100);
	}

	checkTopIndex() {
		const topArray = Array.from(this.visibleRows).filter(
			(elem) =>
				elem.getBoundingClientRect().top <= 64 &&
				elem.getBoundingClientRect().top + elem.offsetHeight >= 64
		);
		console.log(
			topArray,
			Array.from(this.visibleRows).map((elem) => ({
				top: elem.getBoundingClientRect().top,
				bottom: elem.getBoundingClientRect().top + elem.offsetHeight,
			}))
		);

		if (topArray.length) {
			const topElem = topArray[0];
			const newTopIndex = Number(
				topElem.getAttribute("id")?.replace("row-", "")
			);
			if (newTopIndex !== this.topIndex) {
				this.topIndex = newTopIndex;
				this.props.onTopIndexChange(newTopIndex);
			}
		}
	}

	// onScroll() {
	// 	if (!this.ticking && !this.state?.isRendering) {
	// 		this.ticking = true;
	// 		setTimeout(() => {
	// 			// this.checkTopIndex();
	// 			// console.log(this.rows);
	// 			this.ticking = false;
	// 		}, 500);
	// 	}
	// }

	componentDidMount() {
		// window.removeEventListener("scroll", () => this.onScroll());
		// window.addEventListener("scroll", () => this.onScroll(), { passive: true });
		// this.updateNormIndex(this.state.normIndex);
	}

	componentWillUnmount() {
		this.intersectionObserver.disconnect();
		// window.removeEventListener("scroll", () => this.onScroll());
	}

	registerRowElement(element: HTMLDivElement | null, index: number) {
		if (element && !this.observedRows.has(element)) {
			this.observedRows.add(element);
			this.intersectionObserver.observe(element);
		}
	}

	loadMoreTriggered() {
		this.setState({
			renderEndIndex: Math.min(
				this.props.rows.length,
				this.state.renderEndIndex + 10
			),
		});
	}

	render() {
		const renderedRowsRange = range(
			this.state.renderEndIndex - this.state.renderStartIndex,
			this.state.renderStartIndex
		);

		return (
			<div
				ref={this.divElement}
				style={{
					width: "100%",
					maxWidth: "47em",
					marginLeft: "auto",
					marginRight: "auto",
				}}
			>
				{renderedRowsRange.map((index) => (
					<div
						className="row"
						id={`row-${index}`}
						key={index}
						ref={(e) => this.registerRowElement(e, index)}
					>
						<NormView data={this.props.rows[index]}></NormView>
					</div>
				))}
				{this.props.rows.length > this.state.renderEndIndex && (
					<div style={{ textAlign: "center", margin: "2em" }}>
						<Button
							variant="contained"
							onClick={() => this.loadMoreTriggered()}
						>
							Load more
						</Button>
					</div>
				)}
			</div>
		);
	}
}
